import tkiQrcode from "../../components/tki-qrcode"
import { mapState } from 'vuex'
export default {
    computed: {
        ...mapState(['userInfo']),            
        currentTicketObj() {
            return this.ticketList[this.currentTicket]
        },
    },
    components: {tkiQrcode},
    data() {
        return {
            ticketList: [],
            currentTicket: 0,
            currentTime: '',
            timer: null,
            QRStr: '',
            isChange: false,
            src: ''
        }
    },
    onHide() {
        uni.hideLoading()
        clearInterval(this.timer)
    },
    onShow() {
        this.hasLogin()
        this.openTimer()
    },
    watch: {
        currentTime() {                  
            this.changeQR()
        },
        currentTicket () {                
            this.changeQR()
        }
    },
    methods: {  
        qrR(res) {
            this.src = res
        },   
        clickImg: function(){
            var imgUrl = this.currentTicketObj.bindingPhoto;
            uni.previewImage({
                urls: [imgUrl], //需要预览的图片http链接列表，注意是数组
                success: function (res) { },
                fail: function (res) { },
                complete: function (res) { },
            })
        }, 
        cancelBind () {
            this.$tip.confirm('取消绑定后不可撤销，确认解绑么','确认')
            .then((res) => {
                this.confimCancelBind()
            })
            .catch(() => {
                return
            })
        },
        async changeBindPhoto (url) {
            let params = {
                ticketId: this.currentTicketObj.childCode,
                file: url
            }
            const res = await this.$api.updateBindingPhoto(params)
        },
        async confimCancelBind () {                
            let params = {
                bindingName: this.currentTicketObj.bindingName,
                bingingPhoto: this.currentTicketObj.bindingPhoto,
                childCode:this.currentTicketObj.childCode,
                password: this.currentTicketObj.password
            }
            const res = await this.$api.cancelBind(params)
            if (res.code === '0') {
                this.$tip.toast('解绑成功','none') 
                this.currentTicket = 0
                this.$nextTick(() => {
                    this.getTicketList()
                })                      
            } else {                    
                this.$tip.confirm(`解绑失败${res.code}，此票已被核销`,'知道了')
                .then(() => {
                    return
                })
                .catch(() => {
                    return
                })
            }
        },
        hasLogin () {
            let hasLogin = !!this.userInfo
            if (!hasLogin) {
                this.bindLogin()
            } else {                    
                this.getTicketList()
            }
        },   
        bindLogin() {
            uni.reLaunch({
                url: '../login/login',
            });
        },   
        creatQrcode() {
            let temp = new Date()
            this.currentTime = new Date().getTime()
            this.$nextTick(() => {                    
                this.$refs.qrcode._makeCode()
            })
        },
        openTimer() {                
            //定时刷新
            this.timer = setInterval(() => {
                this.currentTime = new Date().getTime()
            },120000)
        },
        handleChangeQR () {
            if (this.isChange) {
                return
            }                
            this.isChange = true
            setTimeout(() => {
                this.isChange = false
            },2000)
            this.currentTime = new Date().getTime()
        },
        changeQR () {   
            if (this.QRStr) {
                this.$refs.qrcode._clearCode()         
            }
            let code = this.currentTicketObj.childCode
            let pcode = this.currentTicketObj.password
            let un = this.currentTicketObj.username
            let t = this.currentTime
            let obj = {
                code,pcode,un,t
            }
            this.QRStr = JSON.stringify(obj)
        },
        goRight () {
            if ( this.ticketList.length - 1 > this.currentTicket) {
                this.currentTicket++
            } else {
                this.$tip.toast('没有更多了','none')
            }
        },
        goLeft () {                
            if ( this.currentTicket <= 0) {                    
                this.$tip.toast('没有更多了','none')
            } else {
                this.currentTicket--
            }
        },
        switchCurrentTicketType () {

        },            
        uploadImg () {
            let that = this
            wx.chooseImage({
                count: 1,
                sizeType: ['compressed'],
                sourceType: ['album','camera'],
                success (res) {
                    const tempFilePaths = res.tempFilePaths
                    that.imgUploading = true
                    wx.uploadFile({
                        // url: 'https://www.gzlxtx.cn:8080/common/uploadPhoto', //测试环境上传图片地址
                        url: 'https://www.gzlxtx.cn/api/common/uploadPhoto', //正式环境上传图片地址
                        filePath: tempFilePaths[0],
                        name: 'file',
                        success (res1) {
                            let temp = JSON.parse(res1.data)
                            if (temp.code === '0') {  
                                that.changeBindPhoto(temp.data)
                            } else {
                                that.$tip.toast(temp.message,'none')
                            }
                        },
                        complete () {
                            setTimeout(() => {                                        
                                that.imgUploading = false
                            }, 1000);                    
                        }
                    })
                }
            })
        },
        async getTicketList () {
            let params = {
                pageNum: 1,
                pageSize: 100,
                username: this.userInfo.username
            }
            const res = await this.$api.bindTicketList(params)
            if (res.code === '0') {
                if (res.data.list.length > 0) {
                    this.ticketList = res.data.list
                    setTimeout(() => {
                        this.creatQrcode()
                    }, 500);
                } else {
                    this.$tip.toast('还未绑定联票,请先去绑定','none')
                    uni.redirectTo({
                        url: '/pages/bindTicket/bindTicket'
                    })
                }
            }
        }
    }
}
